<template>
<!--背景圖-->
<v-app>
    <span>輸入所有905隻寶可夢的名字!</span>
    <el-input type = "text" v-model = "pokemonGuess" auto-complete = "off" placeholder = "寶可夢名字" @change="search" @input="search"></el-input>
    <span>猜對{{count}} / 905</span>

    <table class="table">
      <thead>
          <tr>
              <th v-for="(column, index) in columns" :key="index"> {{column}}</th>
          </tr>
      </thead>
      <tbody>
            <tr v-for="(item, index) in pokemonsData" :key="index">
                <td v-for="(column, indexColumn) in columns" :key="indexColumn">
                    <span v-if="item.flag || column==='No.'">
                        {{item[column]}}
                    </span>
                </td>
          </tr>
      </tbody>
    </table>
</v-app>

</template>

<script>
import pokemonList from '../pokemonList.json'
export default {
    data() {
        return {
            columns: ["No.", "Pokémon"],
            pokemonsData: pokemonList,
            pokemonGuess: '',
            count: 0
        };
        
    },
    mounted: function() {
        for (var i = 0; i < this.pokemonsData.length; i++)
            this.pokemonsData[i].flag = false
    },
    methods: {
        search() {
            let pokeIndex = this.pokemonsData.findIndex(x => x.Pokémon === this.pokemonGuess)
            if (pokeIndex !== -1) {
                this.pokemonsData[pokeIndex].flag = true
                this.pokemonGuess = ''
                this.count += 1
            }
        }
    }
}</script>

<style>

.login-container {
    box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 550px;
    padding: 35px 35px 15px 35px;
    background: #fff
    ;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
}

.signup{
    width:550px;
    text-align:right;
    font-size:14px;
    font-family:PingFang SC;
}

.signupFor{
    margin-bottom: 10px;
    margin-left: 80%;
    padding-bottom: 10px;
}

</style>


